export default {
	data() {
		return {
			// 导航栏配置
			navBackground: '#F0F4F8', // 导航栏背景色 - 与页面背景色保持一致
			navTitleColor: '#004299', // 导航栏标题颜色 - 重财蓝，在浅色背景上使用深色文字
			
			// 表单数据
			formData: {
				enrollmentType: '本科', // 入校类型
				enrollmentDept: '2024, 建筑学院', // 入校院系
				studentId: '' // 学号（选填）
			}
		}
	},
	onLoad() {
		// 页面加载时的逻辑
	},
	methods: {
		/**
		 * 返回上一页
		 */
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		
		/**
		 * 选择入校类型
		 */
		selectEnrollmentType() {
			// 这里可以打开一个选择器或跳转到选择页面
			uni.showActionSheet({
				itemList: ['本科', '硕士研究生', '博士研究生', '专升本', '其他'],
				success: (res) => {
					this.formData.enrollmentType = res.itemList[res.tapIndex];
				}
			});
		},
		
		/**
		 * 选择入校院系
		 */
		selectEnrollmentDept() {
			// 实际应用中，应该跳转到一个更复杂的选择页面
			// 这里简化处理，使用弹窗提示
			uni.showToast({
				title: '请前往院系选择页面',
				icon: 'none'
			});
		},
		
		/**
		 * 提交表单
		 */
		submitForm() {
			// 表单验证
			if (!this.formData.enrollmentType) {
				uni.showToast({
					title: '请选择入校类型',
					icon: 'none'
				});
				return;
			}
			
			if (!this.formData.enrollmentDept) {
				uni.showToast({
					title: '请选择入校院系',
					icon: 'none'
				});
				return;
			}
			
			// 学号为选填，不做必填验证
			
			// 提交数据
			console.log('提交的数据:', this.formData);
			
			// 模拟提交成功
			uni.showLoading({
				title: '提交中...'
			});
			
			setTimeout(() => {
				uni.hideLoading();
				uni.showToast({
					title: '提交成功',
					icon: 'success',
					duration: 2000,
					success: () => {
						// 延迟返回上一页
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							});
						}, 2000);
					}
				});
			}, 1500);
		}
	}
}
